import React, { Component } from 'react';

import QRCode from 'qrcode-react';
import { FileReader } from './fileReader';
import Config from '../../../contract/config.json';
import * as ethers from 'ethers/index.js';
import { Event } from '../../../contract/index'

import { observer, inject } from 'mobx-react'


@inject('ethereumStore')
@observer
export default class Sign extends Component {
    constructor(props) {
        super(props);
        this.state = {
            confirmText: 'Choose File',
            name: '',
            hash: '',
            isExist: '',
            hasConfirmed: true,
            approvers: [],
        }
        this.provider = this.props.ethereumStore.provider;
        this.contract = new ethers.Contract(Config.edoc[3].contractAddress, Config.edoc.abi, this.provider)

    }
    componentWillMount() {
    }
    componentWillReact() {
        this.state.hash && this.getConfirmState();
    }
    getConfirmState = async () => {
        let name = await this.contract.peopleName(this.props.ethereumStore.address);
        let hasConfirmed = false
        if ((await Event.bind(this.provider)(this.contract, 'Approver', [this.props.ethereumStore.address, this.state.hash])).length) {
            hasConfirmed = true;
        }
        let confirmText = 'No Set Name'

        console.log('test')
        if (name) {
            confirmText = 'Confirm';
            if (hasConfirmed) {
                confirmText = 'Confirmed';
            }
        }
        this.setState({ name, hasConfirmed, confirmText, hash: this.state.hash })

        setTimeout(async () => {
            let r = await Event.bind(this.provider)(this.contract, 'DocumentApproved', [this.state.hash])
            r = r.map(async v => {
                let userAddress = v[1];
                let name = await this.contract.peopleName(userAddress);
                return { name, transactionHash: v.transactionHash, blockNumber: v.blockNumber }
            })
            Promise.all(r).then(r => {
                this.setState({ approvers: r })
            })
        })
    }
    onLoaded = (hash) => {
        this.state.hash = hash;
        this.getConfirmState()
    }
    confirm = async () => {
        let signer = this.contract.connect(this.props.ethereumStore.wallet.connect(this.provider));
        let tx
        if ((await Event.bind(this.provider)(this.contract, 'NewDocument', this.state.hash)).length) {
            tx = await signer.documentApproved(this.state.hash)
        } else {
            console.log(signer)
            tx = await signer.newDocument(this.state.hash, [])
        }
        alert('txid: ' + tx.hash)
        setTimeout(async () => {
            await tx.wait();
            this.getConfirmState()
        })
    }

    render() {
        let approvers = this.state.approvers.map((v, i) => {
            let link = `https://ropsten.etherscan.io/tx/${v.transactionHash}`
            return (<tr key={v.transactionHash}>
                <td>{i}</td>
                <td>{v.name}</td>
                <td><a href={link}><QRCode size={150} value={link} /></a></td>
                <td>{v.blockNumber}</td>
            </tr>)
        })
        return (
            <form onSubmit={(e) => e.preventDefault()}>
                <div style={{ display: "none" }}>{this.props.ethereumStore.address}</div>
                <FileReader
                    onLoaded={this.onLoaded}
                >
                    <div className="box-footer">
                        <button
                            className="btn btn-primary" disabled={this.state.hasConfirmed || !this.state.name}
                            onClick={this.confirm}
                        >
                            <i className="fa fa-pencil"></i>
                            <span>
                                {this.state.confirmText}
                            </span>
                        </button>
                    </div>
                </FileReader>

                <div className="box box-primary">
                    <div className="box-header with-border">
                        <h3 className="box-title">簽署者</h3>
                    </div>
                    <div className="box-body">
                        <table className="table table-striped">
                            <tbody><tr>
                                <th style={{ "width": "10px" }}>#</th>
                                <th>簽署者</th>
                                <th>Txid</th>
                                <th>Block</th>
                            </tr>
                                {approvers}
                            </tbody>
                        </table>
                    </div>
                </div>
            </form>
        )
    }
}
export { Sign };