import React, { Component } from 'react'
import Markdown from 'react-markdown'
import utils from 'ethereumjs-util';

export default class FileReader extends Component {
    constructor(props) {
        super(props)
        this.state = {
            data: '',
            type: '',
            hash: ''
        }
    }
    getFile = (e) => {
        let reader = new window.FileReader();
        let target = e.target;
        let file = target.files[0];

        if (target.files.length === 0) {
            return;
        }

        if (file.size / 1024 / 1024 < 50) {
            reader.onprogress = ({ loaded, total }) => {
                this.setState({ hash: Math.round((loaded / total) * 10000) / 100, data: '' })

            }
            reader.onload = (e) => {
                let sha256 = '0x' + utils.sha256(reader.result).toString('hex');
                this.setState({ hash: 'sha256: ' + sha256, data: reader.result, type: file.type })
                this.props.onLoaded(sha256);
            }
        } else {
            this.setState({ hash: 'file size too big', data: '' });
        }
        if (file.type.indexOf('text') >= 0) {
            reader.readAsText(file);
        } else {
            reader.readAsDataURL(file)
        }
        this.setState({ type: '', data: '' })
    }
    render() {
        let view
        let type = this.state.type
        if (type.indexOf('image') >= 0) {
            view = <img style={{ maxWidth: "100%", maxHeight: "600px" }} src={this.state.data} />;
        } else if (type.indexOf('audio') >= 0) {
            view = (
                <audio controls>
                    <source src={this.state.data} type={type} />
                    你的瀏覽器不支持  {type} 。
                </audio>
            );
        } else if (type.indexOf('video') >= 0) {
            view = (
                <video controls>
                    <source src={this.state.data} type={type} />
                    你的瀏覽器不支持 {type} 。
                </video>
            )
        } else if (type.indexOf('markdown') >= 0) {
            view = <Markdown source={this.state.data} />
        } else if (type.indexOf('text') >= 0) {
            view = (<pre>{this.state.data}</pre>)
        } else if (type !== '') {
            view = (<pre>無法處理檔案</pre>)
        }
        return (
            <div className="box box-primary">
                <div className="box-header with-border">
                    <h3 className="box-title">內容顯示</h3>
                </div>
                <div className="box-body">
                    <div className="input-group">
                        <input type="text" className="form-control" placeholder="SHA256" value={this.state.hash} readOnly />
                        <label className="input-group-addon">
                            <i className="fa fa-folder-open-o"></i>
                            <input type="file" style={{ "display": "none" }} onChange={(e) => { this.getFile(e) }} />
                        </label>
                    </div>
                    {view}
                </div>
                {this.props.children}
            </div>
        )
    }
}

export { FileReader }